import { Swiper } from 'swiper/react';
import { Autoplay } from 'swiper/modules';
import { ReactNode } from 'react';

import s from './Carousel.module.scss';
import 'swiper/css';
import 'swiper/css/grid';
import 'swiper/css/pagination';


type PropsT = {
  children: ReactNode
}

function Carousel(props: PropsT) {
  const {
    children
  } = props;

  return (
    <>
      <Swiper
        autoHeight
        slidesPerView={3}
        loop={true}
        autoplay={{
          delay: 2500,
          disableOnInteraction: false,
          pauseOnMouseEnter: true
        }}
        spaceBetween={32}
        modules={[Autoplay]}
        className={s.swiper}
      >
        {children}
      </Swiper>
    </>
  );
}

export { Carousel };
