import mst from '../miniSiteCommon/styleTemplate/style.module.scss';
import { Header } from '../miniSiteCommon/header';
import { Content } from './Content';
import { Footer } from '../miniSiteCommon/footer/';
import { PostFooter } from '../miniSiteCommon/postfooter';
import { memo, useContext, useEffect } from 'react';
import { MiniSiteDataT } from 'templatesReact/miniSiteCommon/types';
import MiniSiteProvider from 'templatesReact/miniSiteCommon/Provider';
import { MiniSiteContext } from 'templatesReact/miniSiteCommon/Context';
import { useNavigate } from 'react-router-dom';
import { AppRoutes } from 'pagesReact';

const MiniSiteReviewTemplate = memo((props: MiniSiteDataT) => {
  return (
    <MiniSiteProvider>
      <_MiniSiteReviewTemplate {...props} />.
    </MiniSiteProvider>
  );
});

const _MiniSiteReviewTemplate = memo((props: MiniSiteDataT) => {
  const { data: { isPremium }, setData } = useContext(MiniSiteContext);

  const navigate = useNavigate();

  useEffect(() => {
    setData && setData(props);
  }, [props]);

  if (!isPremium) {
    navigate(AppRoutes.MINI_SITE);
  }

  return (
    <main className={mst.body}>
      <Header />
      <Content />
      <Footer />
      <PostFooter />
    </main>
  );
});

export { MiniSiteReviewTemplate };
