import { memo, useContext, useState } from 'react';
import { Box } from '../miniSiteCommon/box/Box';
import { CardItem } from '../miniSiteCommon/cardItem/CardItem';
import { Carousel } from 'templatesReact/miniSiteReviewTemplate/Carousel';
import { SwiperSlide } from 'swiper/react';
import s from './Review.module.scss';
import { MiniSiteContext } from 'templatesReact/miniSiteCommon/Context';
import { CustomButton } from 'components/CustomButton/CustomButton';
import { ReviewItemT } from 'templatesReact/miniSiteCommon/types';
import { RemoveReviewItemModal } from './RemoveReviewItemModal';
import { AddReviewItemModal } from './AddReviewItemModal';

const Review = memo(() => {
  const { data: {
    isEdit,
    reviews
  } } = useContext(MiniSiteContext);

  const [ isOpenAdd, setIsOpenAdd ] = useState<boolean>(false);
  const [ isOpenRemove, setIsOpenRemove ] = useState<boolean>(false);
  const [ selectedItem, setSelectedItem ] = useState<ReviewItemT | undefined>(undefined);

  const selectItemForEdit = (id: string) => {
    const foundedItem = reviews.find(item => item.id === id);
    if (foundedItem) {
      setSelectedItem(foundedItem);
      setIsOpenAdd(true);
    }
  };
  const unselectItemForEdit = () => {
    setSelectedItem(undefined);
    setIsOpenAdd(false);
  };

  const selectItemForRemove = (id: string) => {
    const foundedItem = reviews.find(item => item.id === id);
    if (foundedItem) {
      setSelectedItem(foundedItem);
      setIsOpenRemove(true);
    }
  };
  const unselectItemForRemove = () => {
    setSelectedItem(undefined);
    setIsOpenRemove(false);
  };

  const items = (reviews || []).map(item => <SwiperSlide key={item.id}>
    <CardItem
      isCard
      padding='m'
      background='white'
      firstName={item.firstName}
      lastName={item.lastName}
      role={item.role}
      text={item.text}
      rating={item.rating}
      isEdit={isEdit}
      editCallback={() => selectItemForEdit(item.id)}
      removeCallback={() => selectItemForRemove(item.id)}
    />
  </SwiperSlide>);

  return (
    <>
      <Box
        title='Review'
        titleSize='l'
        isBox={false}
        className={s.box}
        childrenMore={isEdit && <CustomButton
          text="Add review"
          variant='action'
          size='l'
          width='content'
          onClick={() => setIsOpenAdd(true)}
        />}
      >
        <Carousel>
          {items}
        </Carousel>
      </Box>

      {/*<AddReviewItemModal*/}
      {/*  isOpen={isOpenAdd}*/}
      {/*  onClose={unselectItemForEdit}*/}
      {/*  onOverlay={unselectItemForEdit}*/}
      {/*  onEscape={unselectItemForEdit}*/}
      {/*  //selectedItem={selectedItem}*/}
      {/*/>*/}
      {/*<RemoveReviewItemModal*/}
      {/*  isOpen={isOpenRemove}*/}
      {/*  onClose={unselectItemForRemove}*/}
      {/*  onOverlay={unselectItemForRemove}*/}
      {/*  onEscape={unselectItemForRemove}*/}
      {/*  //selectedItem={selectedItem}*/}
      {/*/>*/}
    </>
  );
  
});

export { Review };
